const api = require('../../config/api.js');
const util = require('../../utils/util.js')
const user = require("../../services/user.js")
const app = getApp()	
export default {
	data() {
		return {
			list: [],
			params: {
				page: 1,
				size: 10
			},
			isRefresh: false
		}
	},
	//初始化
	onLoad() {
		this.getList();
	},
	// 页面显示
	onShow() {
		
	},
	//分享
	onShareAppMessage() {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/user/index?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		this.params.page = 1;
		this.isRefresh = true;
		this.getList()
	},
	//上拉动作
	onReachBottom(){
		var page = this.params.page+1;
		this.params.page = page;
		this.getList()
	},
	methods: {
		//获取充值记录
		getList() {
			wx.showLoading()
			util.request(api.getRechargeList,this.params,'post').then(res=>{
				wx.hideLoading()
				if(res.code == 0) {
					var list = res.data.list;
					if(this.isRefresh) {
						this.list = list;
					}else {
						var templist = this.list;
						var resultlist = templist.concat(list)
						this.list = resultlist;
					}
				}
			})
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
	},
	filters:{
		formatTimes(times) {
			return util.formatTime(times)
		}
	}
}
